
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import { mapGetters } from 'vuex'
import cookie from '../../../static/js/cookie'
export default {
name: 'Recrult',
components: {
Group,
Checklist,
Selector,
ChinaAddressData,
XAddress
},
data () {
return {
isDisable: false,
id: 0,
typelist: [],
starlist: '',
honorlist: '',
bloodtype: '',
sexes: '',
numscart: '',
realname: '',
wechat: '',
star: '',
mobile: '',
type: [],
honor: '',
experience: '',
resume: '',
userInfo: '',
status: '-1',
addressData: ChinaAddressData,
addressValue: [],
}
},
created () {
this.getTeacherInfo()
this.getCat()

let starlist = [
{key: '水瓶座', value: '水瓶座'},
{key: '双鱼座', value: '双鱼座'},
{key: '白羊座', value: '白羊座'},
{key: '金牛座', value: '金牛座'},
{key: '双子座', value: '双子座'},
{key: '巨蟹座', value: '巨蟹座'},
{key: '狮子座', value: '狮子座'},
{key: '处女座', value: '处女座'},
{key: '天秤座', value: '天秤座'},
{key: '天蝎座', value: '天蝎座'},
{key: '射手座', value: '射手座'},
{key: '摩羯座', value: '摩羯座'},
{key: '金牛座', value: '金牛座'},
]
let honorlist = [
{key: '心理咨询师', value: '心理咨询师'},
{key: '塔罗师', value: '塔罗师'},
{key: '占星师', value: '占星师'},
{key: '魔法师', value: '魔法师'},
{key: '星座达人', value: '星座达人'},
{key: '周易师', value: '周易师'},
]

this.honorlist = honorlist
this.starlist = starlist
},
computed: {
...mapGetters({
setInfo: 'userInfo'
})
},
methods: {
checked (val) {
this.bloodtype = val
},
sex (val) {
this.sexes = val
},
submit () {
if(this.type.length == 0){
this.$vux.toast.text('入驻类别最少选择一个')
return
}
let datas = {
"avatar": 'isempty',
"category": this.type,
"resume": this.resume,
"rank": 0,
"display": 0,
"online": 0,
"rec": 0,
"honor": this.honor,
"experience":  this.experience,
"wechat": this.wechat,
"telphone": this.mobile,
"realname": this.realname,
"bloodtype": this.bloodtype,
"star": this.star,
"sex": this.sexes,
"status": 2,
"numscart": this.numscart,
"mid": cookie.getCookie('mid'),
"area": this.$refs.address.nameValue
}

this.isDisable = true
return this.$api.addOrUpdateTeacher(datas, this.id).then(res => {
this.$vux.toast.text('请等待审核')
setTimeout(() => {
this.$router.push({ path: '/center' })
}, 1000)
})
},

getCat: function () {
return this.$api.catogary().then(res => {
let result = res.data;
let typelist = [];
for(var i=0;i<result.length;i++){
typelist.push({key: result[i].id, value: result[i].title})
}
this.typelist = typelist

if(result){
this.id = result.id
}
})
},

getTeacherInfo: function () {
let id = cookie.getCookie('mid');
if(id){
return this.$api.updateInfo(id).then(res => {
let result = res.data.results[0];
if(result){
this.id = result.id
this.realname = result.realname
this.experience = result.experience
this.honor = result.honor
this.mobile = result.telphone
this.wechat = result.wechat
this.star = result.star
this.numscart = result.numscart
this.bloodtype = result.bloodtype
this.resume = result.resume
this.sexes = result.sex
this.status = result.status
this.addressValue = result.area.split(' ')
this.category = result.type
let typelist = [];
for(var i=0;i<result.type.length;i++){
typelist.push(result.type[i].id)
}
this.type = typelist
}
})
}else{
return this.$api.wechatAuth().then(res => {
sessionStorage.setItem("url", this.$route.path);
window.location.href = res.data.result
})

}
}
}
}



